
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'LifeTree',
components: {
XHeader
},
data() {
return {
list: [],
userInfo: {},
pageIndex: 1,
pageSize: 30,
nodata: false
}
},
mounted() {
this.initdata();
},
methods: {
initdata() {
this.$ajax.post('', {
'MSH.1': 'lifeTree',
'MSH.2': 'qryMylifeTree',
'pageIndex': this.pageIndex,
'GRDAID':this.$root.loginInfo.grdaid,
'pageSize': this.pageSize,
'CHZBM': this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data.rows != 'undefined') { //判断是否空对象
if (data.rows.length > 0) {
this.nodata = false;
this.list = this.list.concat(data.rows);
} else {
this.judge()
}
}else{
this.judge()
}
if (typeof data != 'undefined' && typeof data.userInfo != 'undefined' && this.pageIndex == 1) { //判断是否空对象
this.userInfo = data.userInfo;
}
}).catch(erro=>{
this.judge()
})
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
this.nodata = true;
}
},
pullingUp() { //上拉
this.pageIndex++;
this.initdata();
},
}
}



